*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

@font-face {
  font-family: Roboto;
  src: local(Roboto), local(Roboto-Regular), url("roboto-regular.a2bee839.woff2") format("woff2"), url("roboto-regular.8fc8376c.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Roboto;
  src: local(Roboto Medium), local(Roboto-Medium), url("roboto-medium.2864703e.woff2") format("woff2"), url("roboto-medium.59c24b45.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Roboto;
  src: local(Roboto Bold), local(Roboto-Bold), url("roboto-bold.47509f76.woff2") format("woff2"), url("roboto-bold.bfea707b.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

:root[data-theme="dark"] {
  --page-bgc: #111;
  --hero-bgc-clr: #282828;
  --hero-title-clr: #fff;
  --hero-subtract-clr: url("subtract_black.73ee6931.png");
  --primary-text-clr: #fff;
  --secondary-text-clr: #f8f8f8;
  --muted-text-clr: #b7b7b7;
  --switcher-pos-mob: 18px;
  --switcher-pos-tab: 30px;
  --switcher-pos-desk: 34px;
  --switcher-bow-pos-mob: -16px;
  --switcher-bow-pos-tab: -4px;
  --switcher-bow-pos-desk: 0px;
  --switcher-bow-rotaion: 0;
  --mobile-menu-bgc: #111;
  --backdrop-bgc: #fff;
}

:root[data-theme="light"] {
  --page-bgc: #fff;
  --hero-bgc-clr: #f8f8f8;
  --hero-title-clr: #111;
  --hero-subtract-clr: url("subtract_white.6e4f549b.png");
  --primary-text-clr: #282828;
  --secondary-text-clr: #282828;
  --muted-text-clr: #595959;
  --switcher-pos-mob: -3px;
  --switcher-pos-tab: -3px;
  --switcher-pos-desk: -2px;
  --switcher-bow-pos-mob: -36px;
  --switcher-bow-pos-tab: -36px;
  --switcher-bow-pos-desk: -35px;
  --switcher-bow-rotaion: 180deg;
  --mobile-menu-bgc: #f8f8f8;
  --backdrop-bgc: #000;
}

html {
  cursor: url("cursor-red&black.b6f56834.svg"), pointer;
}

a {
  cursor: url("cursor-red&white.f215fa37.svg"), pointer;
}

button {
  cursor: url("cursor-red&white.f215fa37.svg"), pointer;
}

a:hover {
  cursor: url("cursor-red&white.f215fa37.svg"), pointer;
}

button:hover {
  cursor: url("cursor-red&white.f215fa37.svg"), pointer;
}

svg:hover {
  cursor: url("cursor-red&white.f215fa37.svg"), pointer;
}

.header__theme-switcher:hover {
  cursor: url("cursor-red&white.f215fa37.svg"), pointer;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  outline: none;
  text-decoration: none;
}

button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
}

body {
  color: var(--primary-text-clr);
  letter-spacing: .04em;
  background-color: var(--page-bgc);
  font-family: Roboto, sans-serif;
  font-style: normal;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

body.is-open {
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 704px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1216px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 320px) {
  .section {
    padding: 28px 0;
  }
}

@media screen and (min-width: 768px) {
  .section {
    padding: 40px 0;
  }
}

@media screen and (min-width: 1200px) {
  .section {
    padding: 80px 0;
  }
}

.visually-hidden {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  position: absolute;
}

@keyframes Scale {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.5);
  }
}

.site-header {
  padding: 8px 20px;
}

@media screen and (min-width: 768px) {
  .site-header {
    padding: 12px 32px;
  }
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

@media screen and (min-width: 1200px) {
  .logo {
    margin-right: 90px;
  }
}

.logo-img {
  width: 30px;
  height: 24px;
}

@media screen and (min-width: 768px) {
  .logo-img {
    width: 48px;
    height: 48px;
    margin-right: 8px;
  }
}

.logo-text {
  color: var(--primary-text-clr);
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: none;
}

@media screen and (min-width: 768px) {
  .logo-text {
    font-size: 24px;
    font-weight: 400;
    line-height: 1.16;
    display: inline-block;
  }
}

.header__menu-button {
  display: none;
}

@media screen and (max-width: 767px) {
  .header__menu-button {
    text-transform: uppercase;
    color: var(--muted-text-clr);
    background-color: rgba(0, 0, 0, 0);
    border: none;
    outline: none;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.16;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
  }
}

.nav-list {
  display: none;
}

@media screen and (min-width: 768px) {
  .nav-list {
    gap: 32px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.14;
    display: flex;
  }
}

.nav-list__link {
  text-transform: uppercase;
  color: var(--muted-text-clr);
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

body.current-page__home .nav-list__link.current-page__home, body.current-page__catalog .nav-list__link.current-page__catalog, body.current-page__my-library .nav-list__link.current-page__my-library, .nav-list__link:hover, .nav-list__link:focus {
  color: #f87719;
}

.header__theme-switcher {
  cursor: pointer;
  width: 38px;
  height: 16px;
  background: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  border-radius: 194px;
  align-items: center;
  gap: 14px;
  padding: 5px;
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
  .header__theme-switcher {
    width: 54px;
    height: 20px;
    gap: 24px;
    margin-left: auto;
  }
}

@media screen and (min-width: 1200px) {
  .header__theme-switcher {
    width: 60px;
    height: 24px;
    gap: 24px;
  }
}

.header__theme-switcher:before {
  content: "";
  z-index: 2;
  width: 12px;
  height: 12px;
  transform: translateX(var(--switcher-pos-mob) );
  background-color: #fff;
  border-radius: 50px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 12%;
}

@media screen and (min-width: 768px) {
  .header__theme-switcher:before {
    width: 16px;
    height: 16px;
    transform: translateX(var(--switcher-pos-tab) );
  }
}

@media screen and (min-width: 1200px) {
  .header__theme-switcher:before {
    width: 18px;
    height: 18px;
    transform: translateX(var(--switcher-pos-desk) );
  }
}

.header__theme-switcher:after {
  content: "";
  z-index: 2;
  width: 10px;
  height: 10px;
  transform: translateX(var(--switcher-bow-pos-mob) ) rotate(var(--switcher-bow-rotaion) );
  background-color: rgba(0, 0, 0, 0);
  border: 0 solid #f87719;
  border-right-width: 2px;
  border-radius: 50px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 17%;
  left: 40px;
}

@media screen and (min-width: 768px) {
  .header__theme-switcher:after {
    width: 14px;
    height: 14px;
    transform: translateX(var(--switcher-bow-pos-tab) ) rotate(var(--switcher-bow-rotaion) );
    top: 19%;
  }
}

@media screen and (min-width: 1200px) {
  .header__theme-switcher:after {
    width: 15px;
    height: 15px;
    transform: translateX(var(--switcher-bow-pos-desk) ) rotate(var(--switcher-bow-rotaion) );
    top: 19%;
  }
}

#icon-moon, #icon-sun {
  width: 8px;
  height: 8px;
}

@media screen and (min-width: 768px) {
  #icon-moon, #icon-sun {
    width: 11px;
    height: 11px;
  }
}

@media screen and (min-width: 1200px) {
  #icon-moon, #icon-sun {
    width: 13px;
    height: 13px;
  }
}

.backdrop {
  background-color: var(--backdrop-bgc);
  opacity: .2;
  width: 100%;
  height: 100%;
  z-index: 2;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
}

.backdrop.visually-hidden {
  opacity: 0;
}

.mobile-menu__container {
  margin: 0 auto;
}

@media screen and (max-width: 767px) {
  .mobile-menu__container {
    background-color: var(--mobile-menu-bgc);
    height: 100%;
    z-index: 3;
    width: calc(100% - 116px);
